.education-container{
  margin: 3% 5% 0% 5%;
  display: flex;
  justify-content: flex-start;
  color: whitesmoke;
  background-color: rgba(0,0,0, 0.4);
  border-top-left-radius: 20px;
  border-top-right-radius: 80px;
  border-bottom-right-radius: 20px;
  transition: all 0s;
  .courses{
    margin-left: 30px;
    border-left: 2px solid rgba(4,18,48,.3);
  }
}
@media screen and (max-width: 868px){
  .education-container{
    display: flex;
    justify-content: flex-start;
    font-size: 10px;
    .education{
      margin: 0px;
      padding-left: 15px;
    }
    .courses{
      margin: 0px;
      padding-left: 18px;
    }
  }
}