$focus-color:rgb(255,191,0);

.pdf{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  background-color: rgba(0,0,0, 0.4);
  p{
    color: $focus-color;
    text-transform: uppercase;
  }
  img{
    height: 80vh;
  }
}
@media screen and (max-width: 868px){
  .pdf{
    img{
      height: 65vh;
    }
  }
}