$hover-color: rgb(16,199,216);
$focus-color:rgb(255,191,0);
$background-color: rgb(4,18,48);

.projects-container {
  display: flex;
  height: 100%;
  align-items: center;
  justify-content: space-around;
  color: whitesmoke;
  flex-wrap: wrap;
  .project {
    width: 29%;
    max-height: 80%;
    background-image: url("./assets/treking-gear.png");
    background-position: center;
    background-color: black;
    background-size: cover;
    -webkit-box-shadow: 0 0 5px #000;
    box-shadow: 0 0 5px #000;
    &:nth-child(2){
      background-image: url("./assets/movie4evening.png");
    }
    &:nth-child(3){
      background: $background-color;
    }
    .overlay {
      width: 100%;
      height: 100%;
      display: grid;
      grid-template-columns: 1fr;
      grid-template-rows: 1fr 2fr 2fr 1fr;
      background: rgba(4,18,48,.9);
      color:  whitesmoke;
      opacity: 0;
      transition: all 0.5s;
      .items {
        padding-left: 20px;
        letter-spacing: 3px;
      }
      .head {
        font-size: 30px;
        transform: translateY(40px);
        transition: all 0.7s;
        a{
          color: whitesmoke;
          text-decoration: none;
          font-size: 36px;
        }
        hr {
          display: block;
          width: 0;
          border: none;
          border-bottom: solid 2px $hover-color;
          position: absolute;
          bottom: 0; left: 20px;
          transition: all .5s;
        }
      }
      .desc {
        font-size: 18px;
        line-height: 18px;
        font-weight: bold;
        opacity: 0;
        transform: translateY(40px);
        transition: all .3s;
      }
      .link {
        font-size: 12px;
        opacity: 0;
        letter-spacing: 1px;
        font-family: 'Lato', sans-serif;
        transform: translateY(40px);
        transition: all 0.4s;
        a{
          color: whitesmoke;
          font-size: 26px;
          text-decoration: none;
        }
      }
    }
  }
  .project:hover .overlay {
    opacity: 1;
    & .head {
      transform: translateY(0px);
    }
    & hr {
      width: 80%;
      transition-delay: 0.4s;
    }
    & .desc {
      line-height: 22px;
      transform: translateY(0px);
      opacity: 1;
    }
    & .link {
      transform: translateY(0px);
      transition-delay: 0.6s;
      opacity: 1;
    }
  }
}
@media screen and (max-width: 868px){
  .projects-container{
    flex-direction: column;
    justify-content: space-around;
    height: 94%;
    .project{
      height: 150px;
      width: 350px;
      &:hover .overlay .desc{
        p{
          line-height: 14px;
          font-size: 12px;
        }
      }
      &:hover .overlay .head a{
        font-size: 22px;
      }
      &:hover .overlay hr{
        display: none;
      }

    }
  }
}